<template>
    <div class="food_container">
    <div class="food">
        <header>
            <router-link to="/msite">
                <i class="el-icon-arrow-left"></i>
            </router-link>
            <div>{{title}}</div>
        </header>
        <div class="sort_container">

            <div class="sort_item" @click="add1 = !add1,show1 = !show1, isShow1 = !isShow1" :style="{'color':!add1 ? '#3190e8' : '#333'}">
                <div class="sort_item_container">
                    <div class="sort_item_border">
                        <span>{{add1 ? title : '分类'}}</span>
                        <i :class="add1 ? 'el-icon-caret-bottom' : 'el-icon-caret-top'" :style="{'color':!add1 ? '#3190e8' : '#333'}"></i>
                    </div>
                </div>
                <el-collapse-transition>
                <div class="sort_detail_type" v-show="isShow1">
                    <div class="category_left">
                        <ul>
                            <li class="category_left_li">
                                <div>
                                    <span class="category_left_s1">{{shoppingCategorys.name}}</span>
                                </div>
                                <div>
                                    <span class="category_left_s2">{{shoppingCategorys.count}}</span>
                                </div>
                            </li>

                            <li class="category_left_li" v-for="(pro, index) in shoppingCategory" :key="pro.name" @click="getCategory(pro.sub_categories,index)" :class="{liBackground:changeLeftBackground == index}">
                                <div>
                                    <img :src="'https://fuss10.elemecdn.com/'+ pro.image_url" alt="" class="category_left_img">
                                    <span class="category_left_s1">{{pro.name}}</span>
                                </div>
                                <div>
                                    <span class="category_left_s2">{{pro.count}}</span>
                                    <i class="el-icon-arrow-right category_left_i1" :style="{'color':'#333'}"></i>
                                </div>
                            </li>
                        </ul>
                    </div>

                    <div class="category_right">
                        <ul>
                            <li class="category_right_li" v-for="s in lishop" @click="addmisite(s)">
                                <span class="category_right_s1">{{s.name}}</span>
                                <span class="category_right_s2">{{s.count}}</span>
                            </li>
                        </ul>
                    </div>
                </div>
                </el-collapse-transition>
            </div>

            <div class="sort_item" @click="add2 = !add2,show1 = !show1, isShow2 = !isShow2" :style="{'color':!add2 ? '#3190e8' : '#333'}">
                <div class="sort_item_container">
                    <div class="sort_item_border">
                        <span>排序</span>
                        <i :class="add2 ? 'el-icon-caret-bottom' : 'el-icon-caret-top'"></i>
                    </div>
                </div>
                <el-collapse-transition>
                <div class="sort_detail_type" v-show="isShow2">
                    <ul class="sort_list_container">

                        <li class="sort_list_li" @click="sort1(4)">
                            <i class="el-icon-sort sort_list_i1" style="color: rgb(59, 135, 200)"></i>
                            <p class="sort_list_p1">
                                <span class="sort_list_s1" :style="{'color':indexS==4 ? '#3190e8' : '#333'}">智能排序</span>
                                <i class="el-icon-check sort_p_i" v-show="indexS == 4 ? true : false"></i>
                            </p>
                        </li>

                        <li class="sort_list_li" @click="sort1(5)">
                            <i class="el-icon-location-outline sort_list_i1" style="color:rgb(42, 155, 211);"></i>
                            <p class="sort_list_p1">
                                <span class="sort_list_s1" :style="{'color':indexS==5 ? '#3190e8' : '#333'}">距离最近</span>
                                <i class="el-icon-check sort_p_i" v-show="indexS == 5 ? true:false"></i>
                            </p>
                        </li>

                        <li class="sort_list_li" @click="sort1(6)">
                            <i class="el-icon-medal-1 sort_list_i1" style="color: rgb(240, 115, 115);"></i>
                            <p class="sort_list_p1">
                                <span class="sort_list_s1" :style="{'color':indexS==6 ? '#3190e8' : '#333'}">销量最高</span>
                                <i class="el-icon-check sort_p_i" v-show="indexS == 6 ? true:false"></i>
                            </p>
                        </li>

                        <li class="sort_list_li" @click="sort1(1)">
                            <i class="el-icon-eleme sort_list_i1" style="color: rgb(230, 182, 26)"></i>
                            <p class="sort_list_p1">
                                <span class="sort_list_s1" :style="{'color':indexS==1 ? '#3190e8' : '#333'}">起送价最低</span>
                                <i class="el-icon-check sort_p_i" v-show="indexS == 1 ? true:false"></i>
                            </p>
                        </li>

                        <li class="sort_list_li" @click="sort1(2)">
                            <i class="el-icon-timer sort_list_i1" style="color: rgb(55, 199, 183);"></i>
                            <p class="sort_list_p1">
                                <span class="sort_list_s1" :style="{'color':indexS==2 ? '#3190e8' : '#333'}">配送速度最快</span>
                                <i class="el-icon-check sort_p_i" v-show="indexS == 2 ? true:false"></i>
                            </p>
                        </li>

                        <li class="sort_list_li" @click="sort1(3)">
                            <i class="el-icon-star-off sort_list_i1" style="color: rgb(235, 165, 59);"></i>
                            <p class="sort_list_p1">
                                <span class="sort_list_s1" :style="{'color':indexS==3 ? '#3190e8' : '#333'}">评分最高</span>
                                <i class="el-icon-check sort_p_i" v-show="indexS == 3 ? true:false"></i>
                            </p>
                        </li>

                    </ul>
                </div>
                </el-collapse-transition>
            </div>

            <div class="sort_item" @click="add3 = !add3,show1 = !show1,isShow3 = !isShow3" :style="{'color':!add3 ? '#3190e8' : '#333'}">
                <div class="sort_item_container">
                    <div class="sort_item_border">
                        <span>筛选</span>
                        <i :class="add3 ? 'el-icon-caret-bottom' : 'el-icon-caret-top'"></i>
                    </div>
                </div>
                <el-collapse-transition>
                <div class="screen" v-show="isShow3">
                    <div class="screenC1">
                        <div>配送方式</div>
                        <ul>
                            <li v-for="(f,index) in delivery_modes" :key="index" @click="deliveryM">
                                <i class="el-icon-bicycle" style="color: #3190e8" v-if="isShow4"></i>
                                <i class="el-icon-check" style="color: #3190e8" v-if="!isShow4"></i>
                                <span>{{f.text}}</span>
                            </li>
                        </ul>
                    </div>

                    <div class="screenC1" id="screenC1">
                        <div>商家属性（可以多选）</div>
                        <ul>
                            <li v-for="(f,index) in activity_attributes" :key="index" @click="screenZ(f)">
                                <i class="el-icon-check" style="color: #3190e8" v-if="screenArr[screenArr.indexOf(f.id)]==f.id"></i>
                                <span class="screenC1_span1" v-if="!(screenArr[screenArr.indexOf(f.id)]==f.id)">{{f.icon_name}}</span>
                                <span class="screenC1_span2">{{f.name}}</span>
                            </li>
                        </ul>
                    </div>
                    <div class="screenC2" >
                        <div class="filter_button_style" @click="clearS">清空</div>
                        <div class="filter_button_style" @click="screenS">确定<span v-if="indexV">({{indexV}})</span></div>
                    </div>
                </div>
                </el-collapse-transition>
            </div>


        </div>


        <transition name='fade'>
        <div class="back_cover" v-if="show1"></div>
        </transition>


        <div class="shoplist_container">
            <ul>
                <li class="shop_li" v-for="s in shopping" @click="toShop(s)">
                    <img :src="'https://elm.cangdu.org/img/'+s.image_path" alt="" class="shop_img">
                    <div class="shop_right">
                        <div class="shop_detail"><h4 class="shop_detail_h4"><span class="shop_detail_s1">品牌</span>{{s.name}}</h4>
                            <ul>
                                <li v-for="a in s.supports">
                                    <span>{{a.icon_name}}</span>
                                </li>
                            </ul>
                        </div>
                        <div class="shop_detail2">
                            <div class="shop_detail2_d1">
                                <el-rate
                                        v-model="s.rating"
                                        disabled
                                        text-color="#ff9900"
                                        class="shop_star">
                                </el-rate>
                                <span class="ratingnumber">{{s.rating}}</span>
                                <div class="shop_detail2_d2">月售{{s.recent_order_num}}单</div>
                            </div>
                            <div class="shop_detail2_d3">
                                <span class="shop_detail2_s1">蜂鸟专送</span>
                                <span class="shop_detail2_s2">准时达</span>
                            </div>
                        </div>
                        <div class="shop_detail3">
                            <p class="shop_detail3_p1">￥{{s.float_minimum_order_amount}}起送<span>/</span>配送费约{{s.float_delivery_fee}}元</p>
                            <p class="shop_detail3_p2">
                                <span>{{s.distance}}</span>
                                <span>/</span>
                                <span>{{s.order_lead_time}}</span>
                            </p>
                        </div>
                    </div>
                </li>
            </ul>
        </div>
    </div>
    </div>
</template>

<script>
    export default {
        name: "Food",
        data(){
            return{
                show1:false,
                isShow1:false,
                isShow2:false,
                isShow3:false,
                add1:true,
                add2:true,
                add3:true,
                latitude:"",
                longitude:"",
                geohash:"",
                position:"",
                shopping:[],
                msite:[],
                title:"",
                shoppingCategorys:[],
                shoppingCategory:[],
                lishop:[],
                changeLeftBackground:0,
                indexS:0,
                delivery_modes:[],
                isShow4:true,
                activity_attributes:[],
                indexV:null,
                screenArr:[],
            }
        },
        created(){
            this.title = this.$route.params.title;
            this.msite = JSON.parse(localStorage.getItem("msite"));
            // this.latitude = this.$route.params.latitude;
            // this.longitude = this.$route.params.longitude;

            // 获取坐标
            this.axios.get("/v2/pois/"+this.msite.geohash
            ).then(res=>{
                // console.log(res);
                this.position = res;
            }).catch(err=>{
                console.log(err);
            })

            this.axios.get("/shopping/restaurants",{
                params:{
                    latitude:this.msite.latitude,
                    longitude:this.msite.longitude
                }
            }).then(res=>{
                // console.log(res);
                this.shopping = res;
            }).catch(err=>{
                console.log(err);
            });


            //获取所有商铺分类
            this.axios.get("/shopping/v2/restaurant/category").then(res=>{
                for(let i = 0;i < res.length; i++){
                    if(i<1){
                        this.shoppingCategorys = res[i];
                    }else{
                        this.shoppingCategory.push(res[i]);
                        // console.log(this.shoppingCategory)
                    }
                }
                for(let i = 0;i < res[1].lishop.length; i++){
                    if(i>0){
                        this.lishop.push(res[1].lishop[i]);
                    }
                }
                // console.log(res);
            }).catch(err=>{});

            //配送方式
            this.axios.get("shopping/v1/restaurants/delivery_modes",{
            }).then(res=>{
                // console.log(res);
                this.delivery_modes = res;
            }).catch(err=>{})
            //商家属性
            this.axios.get("shopping/v1/restaurants/activity_attributes",{
            }).then(res=>{
                // console.log(res);
                this.activity_attributes = res;
            }).catch(err=>{});
        },
        methods:{
            getCategory(s,index){
                this.lishop = s;
                this.changeLeftBackground = index;
                event.stopPropagation();
            },

            addmisite(s) {
                //获取分类店铺信息
                this.title = s.name;
                this.axios.get("shopping/restaurants?latitude=" + this.msite.latitude + '&longitude=' + this.msite.longitude + '&restaurant_category_ids[]=' + s.id).then(res => {
                    // console.log(res);
                    this.shopping = res;
                    this.isShow1 = false;
                    this.isShow2 = false;
                    this.isShow3 = false;
                    this.show1 = false;
                }).catch(err => {});
            },

            sort1(id){
                this.indexS = id;
                //获取店铺信息
                this.axios.get("shopping/restaurants?",{
                    params:{
                        latitude:this.msite.latitude,
                        longitude:this.msite.longitude,
                        order_by:id
                    }
                }).then(res=>{
                    this.shopping = res;
                    this.isShow1 = false;
                    this.isShow2 = false;
                    this.isShow3 = false;
                    this.show1 = false;
                    // console.log(res);
                }).catch(err=>{})
            },

            deliveryM(){
                this.isShow4 = !this.isShow4;
                event.stopPropagation();
            },
            screenZ(f){
                event.stopPropagation();
                if(this.screenArr.indexOf(f.id)<0){
                    this.screenArr.push(f.id);
                }else{
                    this.screenArr.splice(this.screenArr.indexOf(f.id),1);
                }

                this.indexV = this.screenArr.length;
            },
            toShop(s){
                this.$router.push("/shop");
                localStorage.setItem("shop",JSON.stringify(s));
            },
            clearS(){
                event.stopPropagation();
                this.screenArr = [];
                this.indexV = "";
            },
            screenS(){
                event.stopPropagation();
                this.screenArr = [];
                this.indexV = "";
                this.isShow4 = true;
                this.isShow3 = false;
                this.show1 = false;
                this.add3 = true;
                this.axios.get("shopping/restaurants?latitude="+this.msite.latitude+'&longitude='+this.msite.longitude+'&support_ids[]='+this.screenArr).then(res=>{
                    // console.log(res)
                    this.shopping = res;
                }).catch(err=>{});
            },
        }
    }
</script>

